import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

import { LoanMenuItem } from './loan-item-menu.model';
import { Placement } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'bb-loan-item-menu',
  templateUrl: './loan-item-menu.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanItemMenuComponent<T = string> {
  /**
   * Menu item
   */
  @Input() items!: LoanMenuItem<T>[];
  /**
   * Label for dropdown menu button
   */
  @Input() dropDownButtonLabel!: string;
  /**
   * placement of the dropdown
   */
  @Input() position: Placement = 'bottom-right';
  /**
   * Emit on menu item select
   */
  @Output() itemSelect = new EventEmitter<T>();
}
